*{
    box-sizing: border-box;
    line-height: 25px;
    color: #222;
    transition: .3s all;
    
}
body{
    margin: 0;
    padding: 0;
}
h1,h2,h3,h4,h5,h6{
    font-weight: 500;
}
h6{
    font-size: 10px;
}
h5{
    font-size: 15px;
}
h4{
    font-size: 20px;
}
h3{
    font-size: 25px;
}
h2{
    font-size: 30px;
}
h1{
    font-size: 35px;
}
button{
    display: inline-block;
    padding: 8px 12px;
}
a,a:hover,a:active{
    color: #000;
    text-decoration: none;
}